
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6, 
form,input,textarea,p,th,td { 
    padding: 0; 
    margin: 0;
}
body {
	font: 0.45rem "微软雅黑";
	min-width: 360px;
}
table { 
    border-collapse: collapse; 
    border-spacing: 0;
}
img { 
    border: 0;
}
ol,ul { 
    list-style: none;
}
th { 
    text-align: left;
}
h1,h2,h3,h4,h5,h6 { 
    font-weight: normal; 
    font-size: 100%;
}
.cls{
	zoom:1;
}
.cls:after{
	display: table;
	content: "";
	clear:both;
}



